<template>
    <div class="notes">
        <button @click="navbarisShow=!navbarisShow" class="btn">≡</button><br/>
        <transition name="navbar">
            <navbar v-show="navbarisShow" class="navbar"></navbar>
        </transition>

        <button @click="isShow=true" class="add" 
        style="border-color:rgb(145,178,253)">+新增</button>
        <input placeholder="标题查询" v-model="searchText" class="searchtext">
        <!-- <button class="searchbtn" @click="search">查询</button> -->

        <span>标题</span>
        <span>分类</span>
        <span>字数</span>
        <span>创建时间</span>
        <form class="writeNotes" v-for="item in noteslist" :key="item.id">
            <span>{{item.title}}</span>
            <span>{{item.type}}</span>
            <span>{{item.text.length}}</span>
            <span>{{item.date}}</span>
            <span @click="handleEdit(item.id)" class="edit">编辑</span>
            <span @click="del(item.id)" class="edit">删除</span><br/>
        </form>

        <div id="overlay" v-show="isShow" @click="isShow=false">
            <div id="center" @click.stop>
                <div>标题<input type="text" v-model='preTitle' class="title" 
                  maxlength="30" 
                  onkeyup="value=value.replace(/[^\w\u4E00-\u9FA5|\s+$]/g, '')"></div>
                分类<select v-model="preType" class="type">
                    <option disabled value="">--请选择--</option>
                    <option>笔记</option>
                    <option>会议</option>
                </select>
                <div>内容</div><textarea type="text" v-model='preText' class="text"
                  maxlength="800" spellcheck="false"
                  onkeyup="value=value.replace(/[^\w\u4E00-\u9FA5|\s+$]/g, '')"></textarea>
                <button @click="clear()" class="cancel" style="border-color:darkgray">取消</button>
                <button @click="add()" class="enter">确定</button>
            </div>
        </div>

        <div id="overlay" v-show="editisShow" @click="editisShow=false">
            <div id="center" @click.stop>
                <div>标题<input type="text" v-model='editTitle' class="title" 
                  maxlength="30"
                  onkeyup="value=value.replace(/[^\w\u4E00-\u9FA5|\s+$]/g, '')"/></div>
                分类<select v-model="editType" class="type">
                    <option disabled value="">--请选择--</option>
                    <option>笔记</option>
                    <option>会议</option>
                </select>
                <div>内容</div><textarea type="text" v-model='editText' class="text"
                    maxlength="800" spellcheck="false"
                    onkeyup="value=value.replace(/[^\w\u4E00-\u9FA5|\s+$]/g, '')"></textarea>
                <button @click="clearEdit()" class="cancel">取消</button>
                <button @click="edit()" class="enter">修改</button>
            </div>
        </div>
    </div>
</template>

<script>
import navbar from '@/components/Navbar.vue'
export default {
  components: {
    navbar
  },
  data () {
    return {
      datalist: [],

      isShow: false,
      editisShow: false,
      navbarisShow: true,

      searchText: '',

      id: 0,
      preTitle: '',
      preType: '',
      preText: '',
      editTitle: '',
      editType: '',
      editText: '',
      editDate: '',
      editId: 0,
      editIndex: 0,

    }
  },
  methods: {
    clear () {
      this.isShow = false
      this.preTitle = ''
      this.preType = ''
      this.preText = ''
    },
    add () {
      if (this.preTitle === '' || this.preType === '' || this.preText === '') {
        alert('请把笔记写完整啦！(/≧▽≦)/')
      } else {
        const date = new Date()
        const notes = {
          title: this.preTitle,
          type: this.preType,
          text: this.preText,
          date: date.getFullYear() + '/' + (date.getMonth() + 1) + '/' + date.getDate(),
          id  : this.id,
        }
        this.datalist.push(notes)
        this.clear()
        this.id++
      }
    },
    del (id) {
      const r = confirm('确定删除吗(ノへ￣、)')
      if (r) {
        for(var i = 0;i<this.id;i++){
          if(this.datalist[i]){
            if(this.datalist[i].id === id){
              this.datalist.splice(i, 1)
            }
          }
        }
      }
    },
    handleEdit (id) {
      for(var i = 0;i<this.id;i++){
        if(this.datalist[i]){
          if(this.datalist[i].id === id){
            this.editIndex = i
          }
        }
      }
      this.editTitle = this.datalist[this.editIndex].title
      this.editType = this.datalist[this.editIndex].type
      this.editText = this.datalist[this.editIndex].text
      this.editDate = this.datalist[this.editIndex].date
      this.editId = id
      this.editisShow = true
    },
    edit () {
      const editNotes = {
        title: this.editTitle,
        type: this.editType,
        text: this.editText,
        date: this.editDate,
        id  : this.editId,
      }
      for(var i = 0;i<this.id;i++){
        if(this.datalist[i]){
          if(this.datalist[i].id === this.editId){
            this.datalist.splice(i, 1, editNotes)
          }
        }
      }
      this.clearEdit()
    },
    clearEdit () {
      this.editTitle = '',
      this.editType = '',
      this.editText = '',
      this.editDate = '',
      this.editId = '',
      this.editisShow = false
    },
    // search(){
      
    // }
  },
  computed: {
    noteslist(){
      return this.datalist.filter(item => item.title.includes(this.searchText))
    }
  }
}
</script>

<style scoped>
    .notes {
        width: 1274px;
        height: 700px;
        background-color: rgb(250, 250, 250);
        opacity: 60%;
    }

    #overlay {
        background-color: rgba(0, 0, 0, 0.6);
        width: 100%;
        margin: auto;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }

    #center {
        background-color: rgb(255, 255, 255);
        border-radius: 5px;
        padding-top: 15px;
        padding-left: 30px;
        padding-bottom: 15px;
        width: 600px;
        height: 350px;
        position: fixed;
        margin: auto;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
    }

    .text {
        height: 200px;
        width: 400px;
    }
    .title{
        height: 25px;

    }
    .type{
        height: 25px;
    }

    .btn{
        width: 66px;
        height: 30px;
        background-color: rgba(255, 0, 195, 0.295);
    }
    .btn:hover{
        background-color: rgba(255, 0, 238, 0.505);
    }
    .add{
        background-color: rgb(233,243,255);
        color: rgb(145,178,253);
        height: 25px;
        border-radius:5px;

    }
    .add:hover{
        background-color: rgb(191, 210, 254);
        
    }
    .writeNotes{
        background-color: white;
    }
    .cancel{
        width: 50px;
        height: 30px;
        background-color: white;
        border-radius:5px;
    }
    .cancel:hover{
      background-color: rgba(169, 169, 169, 0.553);
    }
    .enter{
        width: 50px;
        height: 30px;
        background-color: rgb(64, 158, 255);
        color: white;
        border-radius:5px;
        border: 0;
    }
    .enter:hover{
        background-color: rgba(57, 141, 225, 0.998);
    }
    .edit{
        color:rgb(129,129,222) ;
    }
    .searchtext{
      margin-left: 20px;
    }
    .searchbtn{
      background-color:rgb(129,129,222) ;
      color: white;
      width: 50px;
      height: 25px;
      border: 0;
      margin-left: 20px;
      border-radius:5px;
    }
    .searchbtn:hover{
      background-color: rgba(129, 129, 222, 0.826);
    }
    span{
        display:inline -block;
        width: 200px;
        float:left;
    }
</style>
